@import url('https://fonts.googleapis.com/css2?family=Heebo:wght@900&family=Roboto&family=Share+Tech+Mono&display=swap');

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

a {
    text-underline-offset: 4px;
}

.page {
    position: relative;
    width: 100%;
    height: 100vh;
}

.page-child {
    position: absolute;
    width: 608px;
    height: 1080px;
    color: white;
    font-family: 'Roboto', sans-serif;
    font-weight: 900;
    overflow: visible;
}

h1 {
    margin-top: 0;
    margin-bottom: 50px;
    padding: 0;
    font-family: 'Heebo', sans-serif;
    font-size: 109px;
    line-height: 130px;
    text-align: left;
}

.work-title {
    padding: 28px;
    border: 2px solid white;
    font-family: 'Share Tech Mono', monospace;
    font-size: 35px;
    font-weight: 100;
    letter-spacing: 4px;
    margin-bottom: 40px;
}

.work-type {
    font-family: 'Share Tech Mono', monospace;
    margin-top: 50px;
    font-size: 30px;
    font-weight: 100;
    line-height: 40px;
    padding-bottom: 15px;
}

.skill {
    font-family: 'Share Tech Mono', monospace;
    font-size: 30px;
    font-weight: 100;
    margin-top: 30px;
    flex: 0 0 50%;
}

.skills {
    display: flex;
    flex-wrap: wrap;
    text-align: left;
    padding-bottom: 35px;
}

.skills-column {
    padding-left: 20px;
    padding-right: 20px;
}

img {
    vertical-align: bottom;
}

a {
    color: white !important;
}

a:hover {
    opacity: 0.7;
}

.canvas {
    position: fixed;
    top: 0;
    width: 100%;
    height: 100%;
}

.left {
    left: 0;
}

.right {
    right: 0;
}

.center {
    left: 50%;
    transform: translateX(-50%);
}

.card {
    position: relative;
    opacity: 0;
    padding: 50px;
    box-shadow: none;
    background: rgba(0, 0, 0, 0.5);
    /* background: rgba(36, 33, 89, 0.9); */
    animation-name: card-anim;
    animation-duration: 0.6s;
    animation-delay: 0.1s;
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
}

.card>* {
    opacity: 0;
    animation-name: card-child-anim;
    animation-duration: 0.7s;
    animation-delay: 0.1s;
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
}

.letter:hover {
    opacity: 0.5;
    transform: translateY(-10px);
}

@media only screen and (max-width: 1024px) {}

@keyframes card-anim {
    0% {
        opacity: 0;
        box-shadow: none;
        /* margin-left: 1rem;
    margin-top: 1rem; */
    }

    50% {
        transform: translate(-20px, -20px);
        box-shadow: 20px 20px 0 0 rgba(0, 0, 0, 0.25);
        /* box-shadow: 1.5rem 1.5rem 0 0 rgba(66, 61, 163, 0.8); */
    }

    100% {
        opacity: 1;
        transform: translate(-15px, -15px);
        box-shadow: 15px 15px 0 0 rgba(0, 0, 0, 0.25);
        /* box-shadow: 1rem 1rem 0 0 rgba(66, 61, 163, 0.8); */
    }
}

@keyframes card-child-anim {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

.moon {
    animation-name: moon-anim;
    animation-duration: 60s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
}

@keyframes moon-anim {
    0% {
        transform: rotate(0);
    }

    100% {
        transform: rotate(360deg);
    }
}

html {
    background: linear-gradient(-45deg, #FDB757, #F02E39, #8F42AA);
}

.background-gradient {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-size: 100% 100%;
}

.background-animated {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

@media (max-aspect-ratio: 4/3) {
    .background-animated {
        object-fit: cover;
    }
  }
  

.fade-in {
    opacity: 0;
    animation: fade-in-anim;
    animation-duration: 1s;
    animation-delay: 0.25s;
    animation-fill-mode: forwards;
}

@keyframes fade-in-anim {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

.scroll-for-more {
    position: fixed;
    bottom: 0;
    left: 50%;
    font-family: 'Share Tech Mono', monospace;
    font-size: 20px;
    font-weight: bold;
    color: white;
    transform: translateX(-50%);
    padding-bottom: 20px;
    transition: all 0.5s ease-in-out;
    opacity: 0;
}

.path {
    stroke-dasharray: 50;
    animation: dash 30s linear;
    animation-iteration-count: infinite;
}

@keyframes dash {
    to {
        stroke-dashoffset: 1000;
    }
}